.App {
  background: linear-gradient(120deg, #1b274a, #333D62, #003ea0, #024482);
  background-size: 2000% 2000%;
  animation: background-transition 20s ease infinite;
  min-height: 100vh;
  min-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  color: white !important;
}

@keyframes background-transition {
  0% {background-position:0% 49%}
  50% {background-position:100% 52%}
  100% {background-position:0% 49%}
}

.App-header {
  font-size: 28px;
  margin-top: 1.5em;
  font-weight: 600;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.wrapper {
  border: 1px solid white;
  border-radius: 4px;
  padding: 2em;
  margin-top: 3em;
  margin-left: 1em;
  margin-right: 1em;
  box-sizing: border-box;
  width: 410px;
}

.wrapper:last-child {
  margin-bottom: 3em;
}

.wrapper.no-border {
  border: 0;
  padding: 0;
}

@media (max-width: 450px) {
  .wrapper {
    width: 90%;
  }
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1em;
}

.white-outline fieldset.MuiOutlinedInput-notchedOutline {
  border-color: white !important;
}

.MuiFormControl-root {
  width: 100%;
}

.error {
  color: #d32f2f;
  margin-top: 4px;
  font-size: 12px;
}

p {
  margin: 0 0 1em 0;
}

p:last-child {
  margin-bottom: 0;
}

.emphasis {
  font-weight: 600;
  color: #ccccff;
}
